	module.exports = {"data":{"markdownRemark":{"html":"<h3>Priority vs Normal Payments</h3>\n<h4>Why can’t I choose normal (local) payments instead of priority (swift) payments in a bank transfer?</h4>\n<p>You’re unable to use normal payment options because your bank doesn’t support such options. You’re instead defaulted to using priority payments. Some international payments require a SWIFT code in order for the money to be transferred. Please keep in mind that there’s a fee in processing your payment. If you don’t want to be charged the processing fee, you can look into other payout options such as PayPal or Coinbase. </p>\n<h3>SEPA</h3>\n<h4>Is the transfer provided by CurrencyCloud a SEPA one when choosing EURO currency in the setup?</h4>\n<p>Yes, the transfer is SEPA if you choose for a Normal payment. If you select for a Priority payment, it will go through Swift.</p>\n<h3>Supported Currencies</h3>\n<h4>Why can’t I receive payments in my currency?</h4>\n<p>Our 3 main payout providers are PayPal, Coinbase, and Currencycloud. You can only receive payments in the currencies these 3 options provide. If they don’t support your specified currency, then you unfortunately can’t receive payments in that currency. A work around for this is to receive your payment in US dollars and then have the funds converted to the currency you desire. </p>\n<h3>Payment Tracking</h3>\n<h4>Where is my payment?</h4>\n<p>Keep in mind that it takes about 7-10 days to receive payments after it’s been awarded, and there must be a valid tax form on file as well as a payout method selected.</p>","frontmatter":{"title":"Payments FAQs","id":"hackers/payments-faqs","path":"/hackers/payments-faqs.html","description":null}}},"pathContext":{}}

